import React, {
  ChangeEvent,
  DetailedHTMLProps,
  InputHTMLAttributes,
  KeyboardEvent,
  ReactNode,
  useState,
} from 'react'

import Button from '@mui/material/Button'

import s from './SuperInputText.module.css'

//    
type DefaultInputPropsType = DetailedHTMLProps<
  InputHTMLAttributes<HTMLInputElement>,
  HTMLInputElement
>

//               ,  type
// (   value: string, onChange: ...;      DefaultInputPropsType)
type SuperInputTextPropsType = Omit<DefaultInputPropsType, 'type'> & {
  //  +       
  onChangeText?: (value: string) => void
  onEnter?: () => void
  error?: ReactNode
  spanClassName?: string
}

const SuperInputText: React.FC<SuperInputTextPropsType> = ({
  onChange,
  onChangeText,
  onKeyPress,
  onEnter,
  className,
  spanClassName,
  id,

  ...restProps //       restProps
}) => {
  const [error, setError] = useState<boolean | string>(false)
  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    onChange?.(e) //    onChange,     ( onChange  )
    if (e.currentTarget.value.length > 25) {
      setError('Name is too long, only 25 symbols')
    } else if (e.currentTarget.value.length < 2) {
      setError('Name is too short, must be more than 2 symbols')
    } else setError(false)

    onChangeText?.(e.currentTarget.value)
  }
  const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
    onKeyPress?.(e)
    onEnter && //    onEnter
      e.key === 'Enter' && //     Enter
      !error &&
      onEnter() //   
  }

  const finalSpanClassName = s.error + (spanClassName ? ' ' + spanClassName : '')
  const finalInputClassName =
    s.input +
    (error ? ' ' + s.errorInput : ' ' + s.superInput) +
    (className ? ' ' + s.className : '') //    

  return (
    <div className={s.inputWrapper}>
      <label className={s.label}>Nickname</label>
      <input
        id={id}
        type={'text'}
        onChange={onChangeCallback}
        onKeyPress={onKeyPressCallback}
        className={finalInputClassName}
        {...restProps} //        (value   )
      />
      <Button
        variant="contained"
        href="#contained-buttons"
        size="small"
        disabled={!!error}
        sx={{ width: 52, height: 24 }}
      >
        Save
      </Button>
      <div id={id ? id + '-span' : undefined} className={finalSpanClassName}>
        {error}
      </div>
    </div>
  )
}

export default SuperInputText
